<template>
  <div class="wrap">
    <banner></banner>
    <hr style="border:15px;">
    <carousel></carousel>
    <hr style="border:15px;">
    <video-carousel></video-carousel>
    <futer></futer>
  </div>
</template>
<script>
import Futer from "../components/futer.vue";
import Banner from "../components/banner.vue";
import Carousel from "../components/carousel.vue";
import VideoCarousel from "../components/video-carousel.vue";
export default {
  name: "app",

  data: function() {
    return {};
  },

  components: {
    Futer,
    Banner,
    Carousel,
    VideoCarousel
  }
};

function iframeAutoScale() {
  "use strict";

  var iframes = document.getElementsByTagName("iframe"),
    index = 0;

  if (iframes.length > 0) {
    for (index = 0; index < iframes.length; index++) {
      var iframe = iframes[index],
        parent = iframe.parentElement,
        parentPadding =
          parseInt(
            window
              .getComputedStyle(parent, null)
              .getPropertyValue("padding-left")
          ) +
          parseInt(
            window
              .getComputedStyle(parent, null)
              .getPropertyValue("padding-right")
          ),
        parentWidth = parent.clientWidth - parentPadding,
        ratio = 0.75, // default ratio (4:3)
        width = iframe.clientWidth,
        height = iframe.clientHeight;

      // overwrite default ratio if width and height attributes are not set
      if (width !== undefined && height !== undefined) {
        ratio = height / width;
      }

      iframe.setAttribute("width", parentWidth);
      iframe.setAttribute("height", parentWidth * ratio);
    }
  }
}
// onload
document.addEventListener(
  "DOMContentLoaded",
  function() {
    iframeAutoScale();
  },
  false
);
// on window resize
window.onresize = function(event) {
  iframeAutoScale();
};

$(document).ready(function() {
  // Add smooth scrolling to all links in navbar + footer link
  $(
    ".navbar a, footer a[href='#myPage'], a[href='#details'], a[href='#contact']"
  ).on("click", function(event) {
    // Make sure this.hash has a value before overriding default behavior
    if (this.hash !== "") {
      // Prevent default anchor click behavior
      event.preventDefault();

      // Store hash
      var hash = this.hash;

      // Using jQuery's animate() method to add smooth page scroll
      // The optional number (900) specifies the number of milliseconds it takes to scroll to the specified area
      $("html, body").animate(
        {
          scrollTop: $(hash).offset().top
        },
        900,
        function() {
          // Add hash (#) to URL when done scrolling (default click behavior)
          window.location.hash = hash;
        }
      );
    } // End if
  });

  $(window).scroll(function() {
    $(".slideanim").each(function() {
      var pos = $(this).offset().top;

      var winTop = $(window).scrollTop();
      if (pos < winTop + 600) {
        $(this).addClass("slide");
      }
    });
  });
});
</script>
